export const getUser = async({
  commit,
  getter
}, infoId) => {
  let user = await getPostPms(queryList.userQuery, {
    infoId
  }, 'user', )
  if (!user) {
    console.error('getUser error: ')
    return
  }
  if (!user.info) {
    console.error('404')
    return
  }
  commit('setInfo', user.info)
  commit('setAuthList', user.authList)
}
