// --------------------------mutations--------------------------
// 
export const createVerify = async({
  commit
}, verifyObj) => {
  let successStr = await getPostPms(queryList.createVerifyQuery, {
    verifyObj
  }, 'createVerify')
  if (!successStr) {
    console.error('createVerify error: ')
    return
  }
  // 
  commit('setLoginModel', {
    sending: false,
    canSend: false
  })
  // 
  let count = 60
  let counter = setInterval(() => {
    commit('setLoginModel', {
      codeText: count
    })
    count--
  }, 1000)
  // 
  setTimeout(function() {
    clearInterval(counter)
    commit('setLoginModel', {
      canSend: true
    })
  }, 6000)
}
