export const userLogin = async({
  commit
}, authObj) => {
  let user = await getPostPms(queryList.userLoginQuery, {
    authObj
  }, 'userLogin')
  if (!user) {
    commit('setLoginModel', {
      fail: true
    })
    return
  }
  commit('setUserId', user.infoId)
  commit('setLoginModel', {
    show: false
  })
  Message({
    message: '!',
    type: 'success'
  })
}
