<template>
<div id="login">
  <el-dialog :show-close="false" :visible.sync="modelShow" size="tiny" :lock-scroll="true">
    <div slot="title" class="zh-login-header">
      <h1>
        <template v-if="loginModelType === 'find'">kizun </template>
        <template v-else-if="loginModelType === 'add'">kizun </template>
        <template v-else-if="loginModelType === 'signup'">kizun </template>
        <template v-else>kizun </template>
      </h1>
      <p></p>
    </div>
    <!--  -->
    <div class="zh-login-row">
      <el-input v-model="authAccount" placeholder="" @focus="resetTips"></el-input>
      <p class="zh-login-tip" v-show="accountTip"></p>
    </div>
    <!--  -->
    <div class="zh-login-row">
      <el-input v-model="authCredential" :placeholder="loginModelType === 'find' ? '' : ''" @focus="resetTips"></el-input>
      <p class="zh-login-tip" v-show="credentialTip">6</p>
    </div>
    <!--  -->
    <div class="zh-login-row" v-show="loginModelType !== 'signin'">
      <el-row>
        <el-col :span="16">
          <el-input v-model="verifyCode" placeholder="" @focus="resetTips"></el-input>
          <p class="zh-login-tip" v-show="codeTip"></p>
        </el-col>
        <el-col :span="6" :offset="2">
          <el-button :loading="loginModelSending" :disabled="!loginModelCanSend" type="primary" @click="handleSendCode" class="zh-login-button">
            {{ loginModelCodeText }}
          </el-button>
        </el-col>
      </el-row>
    </div>
    <!--  -->
    <p class="zh-login-tip" v-show="loginModelFail"></p>
    <!-- footer -->
    <div slot="footer">
      <el-button type="primary" size="large" class="zh-login-button" @click="handleSend"> </el-button>
      <el-row>
        <el-col :span="12" class="zh-login-belel" v-show="loginModelType !== 'find'">
          <template v-if="loginModelType === 'signup'"></template>
          <template v-else-if="loginModelType === 'signin'"></template>
          <template v-else></template>
          <span><i class="el-icon-star-on"></i></span>
          <span><i class="el-icon-star-on"></i></span>
          <span><i class="el-icon-star-on"></i></span>
        </el-col>
        <el-col :span="12" class="zh-login-belel right" @click.native="turnToFind" v-show="loginModelType == 'signin'">
          <span> <i class="el-icon-arrow-right"></i></span>
        </el-col>
      </el-row>
    </div>
  </el-dialog>
</div>
</template>

<script>
import {
  mapGetters,
  mapMutations,
  mapActions
} from 'vuex'
export default {
  name: 'zhLogin',
  data() {
    return {
      modelShow: false, //  vuex loginModelShow
      accountTip: false, // 
      credentialTip: false, // 
      codeTip: false, // 
      authAccount: '', // 
      authCredential: '', // 
      verifyCode: '', // 
    }
  },
  computed: {
    ...mapGetters([
      'loginModelShow',
      'loginModelType',
      'loginModelFail',
      'loginModelSending',
      'loginModelCanSend',
      'loginModelCodeText',
    ])
  },
  watch: {
    modelShow(val) {
      this.setLoginModel({
        show: val
      })
    },
    loginModelShow(val) {
      this.modelShow = this.loginModelShow
    }
  },
  methods: {
    ...mapMutations([
      'setLoginModel',
    ]),
    ...mapActions([
      'createVerify',
      'createUser',
      'userLogin',
      'updatePassword',
      'addAuth',
    ]),
    // ()
    turnToFind() {
      this.setLoginModel({
        type: 'find'
      })
    },
    // 
    checkAccount() {
      let isEmail = this.validator.isEmail(this.authAccount)
      let isPhone = this.validator.isMobilePhone(this.authAccount, 'zh-CN')
      let authType = isEmail ? 'email' : (isPhone ? 'phone' : false)
      if (!authType) this.accountTip = true
      return authType
    },
    // 
    handleSendCode() {
      // 
      let authType = this.checkAccount()
      if (!authType) return
      // loading...
      this.setLoginModel({
        sending: true
      })
      // 
      this.createVerify({
        verifyType: authType,
        verifyAccount: this.authAccount
      })
    },
    // 
    handleSend() {
      // 
      let authType = this.checkAccount()
      if (!authType) return
      let rightCredential = this.authCredential.length >= 6
      if (!rightCredential) {
        this.credentialTip = true
        return
      }
      if (this.loginModelType !== 'signin' && !this.verifyCode) {
        this.codeTip = true
        return
      }
      // 
      let authObj = {
        authType,
        authAccount: this.authAccount,
        authCredential: this.authCredential,
      }
      switch (this.loginModelType) {
        case 'signup':
          this.createUser({
            verifyCode: this.verifyCode,
            ...authObj
          })
          break;
        case 'signin':
          this.userLogin(authObj)
          break;
        case 'find':
          this.updatePassword({
            verifyCode: this.verifyCode,
            ...authObj
          })
          break;
        case 'add':
          this.addAuth({
            verifyCode: this.verifyCode,
            ...authObj
          })
          break;
      }
    },
    // 
    resetTips() {
      this.accountTip = this.credentialTip = this.codeTip = false
      this.setLoginModel({
        fail: false
      })
    }
  }
}
</script>

<style lang='stylus'>
@import '~client/assets/base.styl'
.zh-login-header
  text-align: center
  color: $color-text-light
  font-size: $font-size-small
  & h1
    color: $color-text-gray
    font-size: $font-size-medium

.zh-login-button
  width:100%

.zh-login-row:not(:last-child)
  margin-bottom: $ruler-base

.zh-login-tip
  color: $color-text-red
  font-size: $font-size-mini

.zh-login-belel
  margin-top: $ruler-large
  color: $color-text-gray
  font-size: $font-size-small
  text-align:left
  &.right
    text-align: right
  & span
    cursor: pointer
    &:hover
      color: $color-text-cyan
</style>



